  resolveSymbol: (
    symbolName,
    onSymbolResolvedCallback,
    onResolveErrorCallback
  ) => {
    // expects a symbolInfo object in response
    console.log('解析 Symbol', symbolName);
    // console.log('resolveSymbol:',{symbolName})
    let quoteSymbol = symbolName.split('-')[1]
    let quoteToken = lightcone_v3_config.getTokenBySymbol(quoteSymbol)

    var symbol_stub = {
      name: symbolName,
      description: '',
      type: 'crypto',
      session: '24x7',
      timezone: 'Etc/UTC',
      exchange: '',
      minmov: 1,
      pricescale: Math.pow(10, quoteToken.precision),
      // 如果has_intraday为true，则日内周期可用。
      has_intraday: true,
      // 重要: 如果 has_intraday 为 true，则 intraday_multipliers 必须设置，否则“可选分辨率”为空。只能最新'1D'，连“默认分辨率”都自动变为'1D'。
      intraday_multipliers: supportedResolutions.filter(interval => interval < 24 * 60),
      supported_resolution: supportedResolutions,
      volume_precision: quoteToken.precision,
      data_status: 'streaming',
      has_intraday: true,
      has_seconds: true,
      has_daily: true,
      has_weekly_and_monthly: true,
      // 重要: 是否自动填充无交易的 bar。不能填充，否则 MV 变形。
      has_empty_bars: true
    };

    /*
    var split_data = symbolName.split(/[:/]/);
		if (split_data[2].match(/USD|EUR|JPY|AUD|GBP|KRW|CNY/)) {
			symbol_stub.pricescale = 100
    }
    */
    setTimeout(function() {
      onSymbolResolvedCallback(symbol_stub);
      console.log('Resolving that symbol....', symbol_stub);
    }, 0);

    // onResolveErrorCallback('Not feeling it today')
  },
  getBars: function(
    symbolInfo,
    resolution,
    from,
    to,
    onHistoryCallback,
    onErrorCallback,
    firstDataRequest
  ) {
    console.log(
      `获取K线 ${new Date(from * 1000).toLocaleString()} --- ${new Date(to * 1000).toLocaleString()} --- resolution: ${resolution}`
    );

    let market = symbolInfo['name'];

    (async () => {
      var candles = await api.getCandlestick({
        limit: 200,
        market,
        interval: resolution,
        start: from * 1000,
        end: to * 1000,
      })
      candles.reverse()
      console.log('candles解析前:', candles);

      if (candles.length === 0) {
        onHistoryCallback([], { noData: true });
        return
      }

      let quoteSymbol = market.split('-')[1]
      let quoteToken = lightcone_v3_config.getTokenBySymbol(quoteSymbol)

      let startTimestamp = candles[0].start
      let endTimestamp = _.last(candles).start

      console.info('tradingview start ---> ' + (new Date(from * 1000)).toLocaleString())
      console.info('relay start       ---> ' + (new Date(startTimestamp)).toLocaleString())
      console.info('relay end         ---> ' + (new Date(endTimestamp)).toLocaleString())
      console.info('tradingview end   ---> ' + (new Date(to * 1000)).toLocaleString())

      // 重要：这里不能自己填充，否则 MA 的线会宽度变形。应该通过 has_empty_bars 让 tradingview 自己填充。
      // 补充: has_empty_bars 也不行，自动填充上，MA 也会变形。
      let parsedCandles = _.chain(candles)
        // 重要: 这里必须过滤。因为中继有问题，实际返回的范围可能大于请求的范围。
        // 比如请求8~10点，可能获取到8~10:09，那么 10~10:09的上一次已经有了，就会重合。
        // 导致tradingview 增量更新冲突，刷新并重头开始获取。重新获取后冲突，再次重新获取。
        // 中继之所以出现此问题，是因为中继的页数是从前往后的。而且 limit 没用，就取多了。
        .filter(candle => candle.start >= from * 1000 && candle.start <= to * 1000)
        .map(candle => {
          return {
            time: candle.start,
            high: Number(candle['high']),
            low: Number(candle['low']),
            open: Number(candle['open']),
            close: Number(candle['close']),
            volume: lightcone_v3_config.fromWEI(quoteToken.symbol, candle['volume'], quoteToken.precision)
          }
        })
        .value()
      
      console.log('candles解析后:', parsedCandles);
      // if (firstDataRequest) {
      //   var lastBar = bars[bars.length - 1]
      //   history[symbolInfo.name] = {lastBar: lastBar}
      // }
      if (parsedCandles.length) {
        onHistoryCallback(parsedCandles, { noData: false });
      } else {
        onHistoryCallback(parsedCandles, { noData: true });
      }
    })();
  },
  subscribeBars: (
    symbolInfo,
    resolution,
    onRealtimeCallback,
    subscribeUID,
    onResetCacheNeededCallback
  ) => {
    console.log('订阅k线socket', {subscribeUID, onRealtimeCallback, resolution, symbolInfo});
    dispatch.candleStick.subscribeBars({subscribeUID, onRealtimeCallback, resolution, symbolInfo})
  },
  unsubscribeBars: subscriberUID => {
    console.log('去除订阅k线socket');
    dispatch.candleStick.unsubscribeBars(subscriberUID)
  },
  calculateHistoryDepth: (resolution, resolutionBack, intervalBack) => {
    // https://github.com/tradingview/charting_library/wiki/JS-Api#calculatehistorydepthresolution-resolutionback-intervalback
    //optional
    // console.log(
    //   '=====calculateHistoryDepth running: ',
    //   resolution,
    //   resolutionBack,
    //   intervalBack
    // );
    // // while optional, this makes sure we request 24 hours of minute data at a time
    // // CryptoCompare's minute data endpoint will throw an error if we request data beyond 7 days in the past, and return no data
    // return resolution < 60
    //   ? { resolutionBack: 'D', intervalBack: '14' }
    //   : undefined;

    // 重要: 更改获取的时间范围。200 条在此处定制。
    return {
      resolutionBack: 'D',
      intervalBack: _.floor(resolution * 200 / 60 / 24, 2)
    }
  },
