import _ from 'lodash'
import api from 'api'
import { config } from 'librarys/lightcone_v3';
import { notification } from 'antd';

const initState = {
  tokens: [],
}

export default {
  state: initState,
  reducers: {
    update: (state, payload) => ({ ...state, ...payload }),
  },
  effects: dispatch => ({
    async fetchTokens (payload, {  }) {
      let tokens = config.getTokens()
      this.update({ tokens })
    }
  })
}